<!--商品详情-->
<template>
  <div id="detail">
    <!--面包屑-->
    <div class="navigation f12" v-if="detail">
      <!--<a-->
        <!--href="javascript:;"-->
        <!--v-for="(item, index) in detail.navigationList"-->
        <!--@click="navigationTo(item)"-->
      <!--&gt;-->
        <!--{{ item.categoryName }}-->
        <!--<i class="iconfont icon-few" v-if="index < detail.navigationList.length - 1"></i>-->
      <!--</a>-->
      <a href="/index">首页</a> > 商品详情
    </div>
    <div class="product-wrapper">
      <!--图片预览-->
      <div class="preview pr" v-if="detail">
        <!--<div v-if="currentProduct.commodityStatus === 4" class="unusable">下架</div>-->
        <!--<div class="unusable" v-else-if="currentProduct.commodityStock === 0">缺货</div>-->
        <Magnify :previewImg="imageUrl" :zoomImg="imageUrl" />
        <div class="images-wrapper">
          <ul class="image-list" v-if="currentImagesList.length > 0">
            <li
              v-for="(url, index) in currentImagesList"
              :key="index"
              class="image-wrapper"
              @mouseenter="selectCurrentImage(url)"
              :style="(url === imageUrl || (url !== imageUrl) && url.indexOf(imageUrl) > -1) ? 'border: 2px solid #7BBE2B;' : 'border: 2px solid #FFF'"
            >
              <img :src="url">
            </li>
          </ul>
          <i class="iconfont icon-few" v-if="detail.headerImageUrlList.length > 4" @click="setDisplayImages"></i>
          <div class="icon-no-stock" v-if="currentProduct.commodityStatus===3&&currentProduct.commodityStock===0" >已售罄<br/>拼命补货中</div>
        </div>
      </div>
      <img src="../../static/images/default.png" style="width: 430px;" v-else>
      <!--商品详情-->
      <div class="product-detail">
        <h1 v-if="detail">
          <span v-if="currentProduct.presell">【预售】</span>
          {{ currentProduct.commodityName }}
        </h1>
        <div class="product-comment" v-if="detail">{{ currentProduct.commodityComment }}</div>
        <div class="tags-wrapper" v-if="detail && detail.tags">
          <span v-for="(item, index) in currentProduct.tags" class="tag">{{ item }}</span>
        </div>
        <div class="old-price">
          <span class="label">价格：</span>
          <span class="value" v-if="detail">&yen; {{ currentProduct.commodityOriginalPrice.toFixed(2) }}</span>
        </div>
        <div class="sale-price">
          <span class="label">折扣价：</span>
          <span class="value" v-if="detail">&yen; {{ currentProduct.commoditySalesPrice.toFixed(2) }}</span>
        </div>
        <div class="number-wrapper">
          <span class="label">数量：</span>
          <span v-if="detail">
          <button class="reduce-button" @click="reduce" v-if="num > 1">-</button>
          <button class="reduce-button" v-else>-</button>
          <input type="text" v-model.trim="num" class="number" @change="setProductNum">
          <button class="add-button" @click="add" v-if="num < 200 && num < currentProduct.commodityStock">+</button>
          <button class="add-button" v-else>+</button>
          <span class="commodity-stock">库存{{ currentProduct.commodityStock }}件</span>
          <span class="commodity-stock" v-if="num === 200">最多只能购买200件</span>
          </span>
        </div>
        <div class="type-wrapper">
          <span class="label">选择种类：</span>
          <span v-if="detail" style="display: inline-block; width: 600px; vertical-align: top;">
            <span
              style="margin-bottom: 10px"
              v-for="(item, index) in detail.commodityGoodsSpecList"
              class="type"
              :class="(commodityCode === item.commodityCode) ? 'selected' : ''"
              @click="selectProductType(item)"
            >
              {{ item.commoditySpecification }}
              <i class="iconfont icon-chenggong" v-show="commodityCode === item.commodityCode"></i>
            </span>
          </span>
        </div>
        <div class="button-wrapper" v-if="currentProduct && currentProduct.commodityStatus === 3 && currentProduct.commodityStock > 0">
          <button class="buy-now" @click="preCreateOrder()">立即购买</button>
          <button class="add-cart" @click="addCart($event, currentProduct)">加入购物车</button>
        </div>
        <div @click="collect" class="collection" :class="(currentProduct && currentProduct.focus) ? 'collected' : ''">
          <i class="iconfont icon-guanzhu-tianchong"></i>
          <span v-if="currentProduct && currentProduct.focus">取消关注</span>
          <span v-else>关注商品</span>
        </div>
      </div>
    </div>
    <!--猜你喜欢-->
    <h1 class="like-header">猜你喜欢</h1>
    <div class="like-wrapper" v-if="detail">
      <div
        v-for="(item, index) in detail.guessYouLikeCommodityList"
        v-if="index < 5"
        :key="index"
        class="recommend-commodity"
        @click="getProductDetail(item)"
        style="position: relative;"
      >
        <template v-if="item.presell">
          <i class="iconfont icon-yushou"></i>
          <div class="trangle"></div>
        </template>
        <img :src="item.imageUrl">
        <div class="commodity-name">{{ item.commodityName }}</div>
        <div class="commodity-price">&yen; {{ item.commoditySalesPrice.toFixed(2) }}</div>
        <div class="add-cart" @click.stop="addCart($event, item)">加入购物车</div>
      </div>
    </div>
    <!--商品介绍-->
    <div class="product-introduction">
      <div class="tabs-wrapper">
        <span
          v-for="(item, index) in tabs"
          class="tab"
          :class="currentTabIndex === index ? 'active' : ''"
          @click="selectTab(index)"
        >
          {{ item.name }}
          <span v-if="detail && index === 2">({{ detail.countComment }})</span>
        </span>
      </div>
      <!--商品属性和图片-->
      <div v-show="currentTabIndex === 0">
        <div class="props-wrapper" v-if="detail">
          <span
            v-for="(prop, index) in currentProduct.commodityProperties"
            class="prop"
          >
            {{ prop.commodityPropertyName }}：{{ prop.commodityPropertyValue }}
          </span>
        </div>
        <!--推荐商品-->
        <!--
        <h1 class="recommend-header">推荐商品</h1>
        <div class="recommend-wrapper" v-if="detail">
          <div
            v-for="(item, index) in currentProduct.recommendCommodityList"
            v-if="index < 5"
            :key="index"
            class="recommend-commodity"
            @click="getProductDetail(item)"
            style="position: relative;"
          >
            <template v-if="item.presell">
              <i class="iconfont icon-yushou"></i>
              <div class="trangle"></div>
            </template>
            <img :src="item.imageUrl">
            <div class="commodity-name">{{ item.commodityName }}</div>
            <div class="commodity-price">&yen; {{ item.commoditySalesPrice.toFixed(2) }}</div>
            <div class="add-cart" @click.stop="addCart($event, item)">加入购物车</div>
          </div>
        </div> -->
        <div style="overflow: hidden;">
          <div v-if="detail" style="float: left; width: 880px;">
            <img
              v-for="(introduceImageUrl, index) in currentProduct.introduceImageList"
              :src="introduceImageUrl"
              :key="index"
              style="width: 880px"
            >
          </div>
          <div style="float: right;">
            <!--推荐商品-->
            <h1 class="recommend-header" style="text-indent: 30px;">推荐商品</h1>
            <div class="recommend-wrapper" v-if="detail">
              <div
                v-for="(item, index) in currentProduct.recommendCommodityList"
                v-if="index < 5"
                :key="index"
                class="recommend-commodity"
                @click="getProductDetail(item)"
                style="position: relative;"
              >
                <template v-if="item.presell">
                  <i class="iconfont icon-yushou"></i>
                  <div class="trangle"></div>
                </template>
                <img :src="item.imageUrl">
                <div class="commodity-name">{{ item.commodityName }}</div>
                <div class="commodity-price">&yen; {{ item.commoditySalesPrice.toFixed(2) }}</div>
                <div class="add-cart" @click.stop="addCart($event, item)">加入购物车</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--售后保障-->
      <dl v-if="detail && currentTabIndex === 1" class="custom-service">
        <dt>生鲜换货渠道</dt>
        <dd>生鲜类商品（包括蔬菜水果、鲜蛋肉禽、海鲜水产等）和部分特殊商品（商品说明中会提示）时效性较强，为保证商品品质，请在配送完成后及时取货，并在现场取货时第一时间验收商品，如出现品质或数量缺失问题，可立马向工作人员反映，我们将及时为您安排换货或补发商品，一旦签收，我们将默认商品质量和数量均无问题，此后不再接受换货要求。</dd>
       <dt>专业生鲜客服团队—让您售后无忧</dt>
       <dd>银犁食品客服电话：400-1111-028</dd>
       <dd>服务时间：09:00-18:00</dd>
       <dt>银犁食品售后政策</dt>
       <dd>注：图片及信息仅供参照，商品将以实物为准。因拍摄灯光及不同显示器色差等问题可能造成商品图片与实物有一定色差，不属质量问题。如因客户个人原因造成商品出现质量问题的，恕不换货。</dd>
       <dt>权责声明：</dt>
       <dd>银犁食品上的所有商品信息、客户评价等内容，是银犁食品重要的经营资源，未经许可，禁止非法转载使用。</dd>
       <dt>价格说明：</dt>
       <dd>价格：商品的银犁食品销售价，是您最终购买商品的价格依据。</dd>
       <dd>市场价：商品在各大超市和菜市场的平均参考价，该价格可能是品牌专柜标价、商品吊牌价或由品牌供应商提供的正品零售价（如厂商指导价、建议零售价等）；由于地区、时间的差异性和市场行情波动，超市价或菜市场的价格等可能会与您购物时展示的不一致，该价格仅供您参考。</dd>
      </dl>
      <!--评论-->
      <!-- <div v-if="currentTabIndex === 2" class="comment-wrapper">
        <div class="praise-wrapper">
          <span class="praise-name">好评度</span>
          <span class="praise-value" v-if="detail">{{ detail.praise }}%</span>
        </div>
      </div> -->
    </div>
    <Flyball ref="flyball" />
    <Bar/>
  </div>
</template>
<script>
import Bar from '@/components/Bar.vue'
import Magnify from "@/components/Magnify.vue"
import Flyball from '@/components/Flyball.vue'

export default {
  name: 'Detail',
  components: {
    Magnify,
    Flyball,
    Bar
  },
  data () {
    return {
      imageUrl: null,
      detail: null,
      listIndex: 0,
      num: 1,
      currentTabIndex: 0,
      currentImage: null,
      commodityCode: null,
      tabs: [{
        name: '商品介绍'
      }, {
        name: '售后保障'
      } /*, {
        name: '评价'
      }*/]
    }
  },
  computed: {
    currentImagesList () {
      if (this.detail && this.detail.commodityGoodsSpecList) {
        return this.currentProduct.headerImageUrlList.slice(this.listIndex, this.listIndex + 4)
      } else {
        return this.detail.headerImageUrlList.slice(this.listIndex, this.listIndex + 4)
      }
    },
    currentProduct () {
      if (this.detail && this.detail.commodityGoodsSpecList) {
        let result = this.detail.commodityGoodsSpecList.find(element => element.commodityCode === this.commodityCode)
        if (result) {
          return result
        } else {
          this.commodityCode = this.detail.commodityGoodsSpecList[0].commodityCode
          return this.detail.commodityGoodsSpecList[0]
        }
      }
      if (this.detail && !this.detail.commodityGoodsSpecList) return this.detail
    },
    loginStatus () {
      return this.$store.state.loginStatus
    }
  },
  watch: {
    // 检测在商品详情页时，点击分类的推荐商品，更新页面数据
    '$route' () {
      this.getCommodityDetail()
    }
  },
  created () {
    this.getCommodityDetail()
    window.scrollTo(0, 0)
  },
  methods: {
    // 关注商品
    collect () {
      // 如果已经关注了，则取消
      if (this.currentProduct.focus) {
        this.axios.get(this.api.removeCollection + '?commodityCode=' + this.currentProduct.commodityCode).then(res => {
          if (res.status === 200 && res.data.code < 300) {
            this.currentProduct.focus = false
          }
        })
      } else {
        this.axios.get(this.api.collect + '?commodityCode=' + this.currentProduct.commodityCode).then(res => {
          if (res.status === 200 && res.data.code < 300) {
            this.currentProduct.focus = true
          }
        })
      }
      // 获取关注商品信息
      // this.axios.get(this.api.getCollection + '?id=' + this.currentProduct.focusId).then(res => {})
    },
    preCreateOrder(dt){
      const currentProduct=this.currentProduct;
      const data={commodityCode:currentProduct.commodityCode,num:this.num,categoryCode:currentProduct.categoryCode,secKill:currentProduct.secKill,presell:currentProduct.presell}
      sessionStorage.setItem("toPayCommodityList",JSON.stringify([data]))
      if (this.loginStatus) {
        this.$router.push({name:'prePay'})
      } else {
        this.$router.push({ name: 'Login', query: { redirect: 'prePay' }})
      }
    },
    // 获取商品详情
    getCommodityDetail () {
      // 详情内切换商品，重置当前图片
      this.imageUrl = null
      let params = {
        commodityCode: this.$route.params.commodityCode,
        introduceImageWidth: 880
      }
      this.axios.post(this.api.getCommodityDetail, params).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          res.data.result.commodityStock = Math.floor(res.data.result.commodityStock)
          if (res.data.result.commodityGoodsSpecList) {
            res.data.result.commodityGoodsSpecList.forEach(element => {
              element.commodityStock = Math.floor(element.commodityStock)
            })
          }
          this.detail = res.data.result
          this.commodityCode = this.$route.params.commodityCode
          this.selectCurrentImage(this.detail.imageUrl)
        }
      })
    },
    // 面包屑导航
    navigationTo (item) {
      this.$router.push(`/list?category=${item.categoryCode}`)
      console.log(item)
    },
    // 选择当前图片
    selectCurrentImage (imageUrl) {
      this.imageUrl = imageUrl
    },
    // 设置当前展示图片列表
    setDisplayImages () {
      let index = this.listIndex
      if (index + 4 >= this.detail.headerImageUrlList.length) {
        this.listIndex = 0
      } else {
        this.listIndex += 4
      }
    },
    // 选择产品类型
    selectProductType (item) {
      this.commodityCode = item.commodityCode
      // 切换产品类型时重置数量
      this.num = 1
      this.selectCurrentImage(item.imageUrl)
    },
    // 设置手动输入商品数量
    setProductNum () {
      const regex = /\D/g
      let num = this.num = parseInt(this.num.replace(regex, '')) || 1
      if (num > 200 && this.currentProduct.commodityStock > 200) {
        this.num = 200
      } else if (num > 200 && this.currentProduct.commodityStock <= 200) {
        this.num = this.currentProduct.commodityStock || 1
      } else if (num > this.currentProduct.commodityStock) {
        this.num = this.currentProduct.commodityStock || 1
      }
    },
    // 选择tab
    selectTab (index) {
      this.currentTabIndex = index
    },
    // 增加商品数量
    add () {
      this.num++
    },
    // 减少商品数量
    reduce () {
      this.num--
    },
    // 加入购物车
    addCart (event, item) {
      // 如果已经登录，则保存到服务器
      if (this.loginStatus) {
        let param = {
          commodityCode: item.commodityCode,
          num: this.num,
          secKill: false
        }
        this.axios.post(this.api.save, param).then(res => {
          if (res.status === 200 && res.data.code === 200) {
            // 加入购物车动画
            this.$refs.flyball.drop(event)
            setTimeout(() => {
              this.$store.dispatch('getCartNum')
            }, 600)
          } else {
            this.$message({
              message: res.data.remark,
              type: 'warning'
            })
          }
        })
      } else {
        // 如果未登录，则保存到缓存
        this.$router.push({ name: 'Login', query: {'redirect': 'Detail', commodityCode: item.commodityCode }})
      }
    },
    // 获取商品详情
    getProductDetail (item) {
      this.$router.push({name: 'Detail', params: {commodityCode: item.commodityCode}})
      this.getCommodityDetail()
      this.num = 1
      window.scrollTo(0, 0)
    }
  }
}
</script>
<style lang="stylus">
