          font-size: 18px
          color: #fff
          cursor: pointer
          outline: none
          border: none
        .add-cart
          margin-left: 10px
          background: #7bbe2b
          border-radius: 4px
          height: 50px
          width: 160px
          font-size: 18px
          color: #fff
          cursor: pointer
          outline: none
          border: none
      .collection
        margin-top: 20px
        font-size: 12px
        cursor: pointer
        i
          vertical-align: middle
        &.collected
          color: #FF4E00
  .like-header
    margin: 100px 50px 20px
    color: #666
  .like-wrapper
    margin-bottom: 30px
    .recommend-commodity
      display: inline-block
      position: relative
      padding: 0 1rem
      width: 180px
      overflow: hidden
      cursor: pointer
      text-align: center
      img
        width: 144px
        height: 144px
      .commodity-name
        margin-top: 5px
        font-size: 14px
        color: #666
        overflow: hidden
        text-overflow: ellipsis
        white-space: nowrap
      .commodity-price
        margin-top: 5px
        font-size: 14px
        font-weight: 700
        color: #ff4e00
      .add-cart
        position: absolute
        bottom: -50px
        width: 180px
        height: 50px
        line-height: 50px
        font-weight: 700
        text-align: center
        color: #333
        background: #ffc000
        cursor: pointer
        transition: bottom 0.3s
        -moz-transition: bottom 0.3s /* Firefox 4 */
        -webkit-transition: bottom 0.3s /* Safari 和 Chrome */
        -o-transition: bottom 0.3s /* Opera */
      &:hover
        .add-cart
          bottom: 0
          transition: bottom 0.3s
          -moz-transition: bottom 0.3s /* Firefox 4 */
          -webkit-transition: bottom 0.3s /* Safari 和 Chrome */
          -o-transition: bottom 0.3s /* Opera */
  .recommend-header
    margin: 0 50px 20px
    color: #666
  .recommend-wrapper
    margin-bottom: 30px
    .recommend-commodity
      display: block
      position: relative
      padding: 0 1rem
      width: 180px
      overflow: hidden
      cursor: pointer
      text-align: center
      img
        width: 144px
        height: 144px
      .commodity-name
        margin-top: 5px
        font-size: 14px
        color: #666
        overflow: hidden
        text-overflow: ellipsis
        white-space: nowrap
      .commodity-price
        margin-top: 5px
        font-size: 14px
        font-weight: 700
        color: #ff4e00
      .add-cart
        position: absolute
        bottom: -50px
        width: 180px
        height: 50px
        line-height: 50px
        font-weight: 700
        text-align: center
        color: #333
        background: #ffc000
        cursor: pointer
        transition: bottom 0.3s
        -moz-transition: bottom 0.3s /* Firefox 4 */
        -webkit-transition: bottom 0.3s /* Safari 和 Chrome */
        -o-transition: bottom 0.3s /* Opera */
      &:hover
        .add-cart
          bottom: 0
          transition: bottom 0.3s
          -moz-transition: bottom 0.3s /* Firefox 4 */
          -webkit-transition: bottom 0.3s /* Safari 和 Chrome */
          -o-transition: bottom 0.3s /* Opera */
  .product-introduction
    margin-top: 100px
    .tabs-wrapper
      height: 50px
      line-height: 50px
      border-top: 1px solid #CCC
      border-bottom: 1px solid #CCC
      font-size: 14px
      color: #666
      .tab
        display: inline-block
        height: 100%
        padding: 0 2rem
        cursor: pointer
        &.active
          color: #FFF
          background: #FFC000
    .props-wrapper
      margin: 20px 0
      .prop
        display: inline-block
        width: 200px
        font-size: 12px
        color: #666
  .custom-service
    color: #666
    dt
      margin-top: 1rem
      font-size: 14px
      font-weight: 700
    dd
      font-size: 12px
      margin-left: 0
  .comment-wrapper
    .praise-wrapper
      margin-top: 20px
      .praise-name
        font-size: 14px
        color: #666
      .praise-value
        margin-left: 10px
        font-size: 36px
        color: #ffc000
        vertical-align: -9px
  .icon-yushou
    top: -5px
    right: 5px
</style>



